<template>
  <main>
    <div class="box">
      <div class="info">
        <h1>Simple Theme Manager</h1>
        <hr />
        <p>
          In auto mode you can switch the theme on your device and the app theme
          will be updated.
        </p>
      </div>

      <fieldset>
        <legend>Select a theme:</legend>
        <div v-for="theme in themes" :key="theme.id">
          <label>
            <input v-model="current_theme_mode" type="radio" name="theme" :value="theme.value"
              :checked="current_theme_mode === theme.value" />
            {{ theme.label }}
          </label>
        </div>
      </fieldset>

      <div class="actions">
        <button @click="applyTheme">APPLY SETTINGS</button>
        <a @click="resetTheme">Reset theme</a>
      </div>
    </div>
  </main>
</template>

<script>
import theme_manager from 'simple-theme-manager';

export default {
  name: 'App',
  data() {
    return {
      current_theme_mode: null,
      themes: [
        {
          id: 0,
          value: 'auto',
          label: 'Auto'
        },
        {
          id: 1,
          value: 'light',
          label: 'Light'
        },
        {
          id: 2,
          value: 'dark',
          label: 'Dark'
        }
      ]
    }
  },

  mounted() {
    theme_manager.init();
    this.getThemeMode();
  },
  methods: {
    applyTheme() {
      theme_manager.apply(this.current_theme_mode);
      this.getThemeMode();
    },
    resetTheme() {
      theme_manager.reset();
      this.getThemeMode();
    },
    getThemeMode() {
      this.current_theme_mode = theme_manager.getMode();
    }
  }
}
</script>

